import { Component, OnInit } from '@angular/core';
import { ToolsDataFlowService, ToolsDataFlowDetailService } from 'src/app/sdk/user/services';
import { DirektoriPegawaiMasterService, CompanyDivisiService, CompanyJabatanService, CompanyDepartemenService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Data_flow, Data_flow_detail } from 'src/app/sdk/user/models';
import { Pegawai_master, Kantor, Departemen, Divisi, Jabatan } from 'src/app/sdk/hr/models';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private toolsDataFlowService: ToolsDataFlowService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private toolsDataFlowDetailService: ToolsDataFlowDetailService,
        private companyDivisiService: CompanyDivisiService,
        private companyJabatanService: CompanyJabatanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private Pesanservice: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }

    dataFlow: Data_flow = {}
    listDetail: Data_flow_detail[] = []
    id_data_flow: string = ''

    listPegawai: Pegawai_master = {}
    dataKantor: Kantor = {}
    dataDepartemen: Departemen = {}
    dataDivisi: Divisi = {}



    ngOnInit() {
        this.id_data_flow = this.activatedRoute.snapshot.paramMap.get('id_data_flow')
        this.getData()
        this.getDetail()


    }

    getData() {
        this.spinner.show()
        this.toolsDataFlowService.getToolsDataFlowsId(this.id_data_flow).subscribe(
            data => {
                this.dataFlow = data.data;
                this.getKantor(data.data.id_kantor);
                this.getDivisi(data.data.id_divisi);
                this.getDepartemen(data.data.id_departemen);
                this.spinner.hide()
            }
        )
    }

    getKantor(id_kantor) {
        this.companyKantorService.getCompanyKantorsId(id_kantor).subscribe(
            data => {
                this.dataKantor = data.data;
            }
      )
    }

    getDivisi(id_divisi) {
        this.companyDivisiService.getCompanyDivisisId(id_divisi).subscribe(
            data => {
                this.dataDivisi = data.data;
            }
      )
    }

    getDepartemen(id_departemen) {
        this.companyDepartemenService.getCompanyDepartemensId(id_departemen).subscribe(
            data => {
                this.dataDepartemen = data.data;
            }
      )
    }


    getDetail() {
        let filter = {
            id_data_flow: this.id_data_flow
        }
        this.toolsDataFlowDetailService.getToolsDataFlowDetails(JSON.stringify(filter)).subscribe(
            res => {
                this.listDetail = res.data;
            }
        )

    }

    goToBack() {
        this.router.navigate(['/users/approval/data-flow/list'])
    }


}
