import { Jabatan } from 'src/app/sdk/hr/models/jabatan';
import { Divisi } from 'src/app/sdk/hr/models/divisi';
import { Departemen } from 'src/app/sdk/hr/models/departemen';
import { Kantor } from 'src/app/sdk/hr/models/kantor';
import { Data_flow_detail } from 'src/app/sdk/user/models/data-_flow-_detail';
import { CompanyDivisiService, CompanyJabatanService, CompanyDepartemenService, CompanyKantorService } from 'src/app/sdk/hr/services';
import { Component, OnInit } from '@angular/core';
import { ToolsDataFlowService, ToolsDataFlowDetailService } from 'src/app/sdk/user/services';
import { DirektoriPegawaiMasterService } from 'src/app/sdk/hr/services/direktori-pegawai-master.service';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { Pegawai_master } from 'src/app/sdk/hr/models/pegawai-_master';
import { FormControl, Validators } from '@angular/forms';
import { Data_flow } from 'src/app/sdk/user/models';

@Component({
    selector: 'app-add',
    templateUrl: './add.component.html',
    styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private toolsDataFlowService: ToolsDataFlowService,
        private direktoriPegawaiMasterService: DirektoriPegawaiMasterService,
        private toolsDataFlowDetailService: ToolsDataFlowDetailService,
        private companyDivisiService: CompanyDivisiService,
        private companyJabatanService: CompanyJabatanService,
        private companyDepartemenService: CompanyDepartemenService,
        private companyKantorService: CompanyKantorService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private Pesanservice: PesanService,
        private router: Router
    ) { }

    dataFlow: Data_flow = {}
    listDetail: Data_flow_detail[] = []
    id_data_flow: string = ''

    listPegawai: Pegawai_master[] = []
    listKantor: Kantor[] = []
    listDepartemen: Departemen[] = []
    listDivisi: Divisi[] = []
    listJabatan: Jabatan[] = []

    toogleEdit = false;
    toogleAdd = false;
    ngOnInit() {
        this.form()
        this.beforeCreate()

        this.getPreeData();

    }

    toogleAddShow() {
        this.toogleAdd = true
    }
    //-------------------------------------
    getPreeData() {
        this.direktoriPegawaiMasterService.getDirektoriPegawaiMasters().subscribe(
            data => {
                this.listPegawai = data.data;
            }
        )
        this.companyDivisiService.getCompanyDivisis().subscribe(
            data => {
                this.listDivisi = data.data;
            }
        )
        this.companyJabatanService.getCompanyJabatans().subscribe(
            data => {
                this.listJabatan = data.data;
            }
        )
        this.companyKantorService.getCompanyKantors().subscribe(
            data => {
                this.listKantor = data.data;
            }
        )
        this.companyDepartemenService.getCompanyDepartemens().subscribe(
            data => {
                this.listDepartemen = data.data;
            }
        )
    }



    status: string = '';
    id_pegawai: string = '';
    sebagai: string = '';
    button_next_text: string = '';
    pesan: string = '';
    order_by: string = '';
    keterangan: string = '';
    reject: number = 1;
    tambahDetail() {
        let param = {
            id_data_flow: this.id_data_flow,
            status: this.status,
            id_pegawai: this.id_pegawai,
            sebagai: this.sebagai,
            pesan: this.pesan,
            order_by: this.order_by,
            reject: this.reject,
            keterangan: this.keterangan,
            button_next_text: this.button_next_text,

        }

        this.toolsDataFlowDetailService.postToolsDataFlowDetails(param).subscribe(
            res => {
                this.toogleAdd =false;
                this.getDetail();
            }
        )
    }

    toogleEditShow (id) {
        this.toogleEdit = true;
        this.colom()
        this.getFlowDetail(id)
    }

    getDetail() {
        let filter = {
            id_data_flow: this.id_data_flow
        }
        this.toolsDataFlowDetailService.getToolsDataFlowDetails(JSON.stringify(filter)).subscribe(
            res => {
                this.listDetail = res.data;
            }
        )

    }
    deleteDetail(id) {
        this.spinner.show()
        this.toolsDataFlowDetailService.deleteToolsDataFlowDetailsId(id).subscribe(
            data => {
                this.getDetail()
                this.spinner.hide()
            }
        )
    }

    id_data_flow_detai: string = ''
    flowDetail: Data_flow_detail = {}
    getFlowDetail(id) {
        this.toolsDataFlowDetailService.getToolsDataFlowDetailsId(id).subscribe(
            data=> {
                this.detailFlow.setValue(data.data);
                this.id_data_flow_detai = data.data.id_data_flow_detail

            }
        )
    }


    detailFlow: FormGroupTypeSafe<Data_flow_detail>;
    colom() {
        this.detailFlow = this.fb.group<Data_flow_detail>(
            {
                status: new FormControl('',[Validators.required]),
                id_pegawai: new FormControl(''),
                sebagai: new FormControl(''),
                button_next_text: new FormControl(''),
                pesan: new FormControl(''),
                id_data_flow: new FormControl(this.id_data_flow),
                id_data_flow_detail: new FormControl(''),
                reject: new FormControl(''),
                order_by: new FormControl(''),
                keterangan: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }

    UpdateDetail() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.Pesanservice.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.flowDetail = this.detailFlow.value;
        this.toolsDataFlowDetailService.putToolsDataFlowDetailsId({ id: this.id_data_flow_detai, body: this.flowDetail }).subscribe(
            data => {
                this.toogleEdit = false
                this.getDetail()
                this.spinner.hide()
            }, err => {
                this.Pesanservice.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }

    Cancel() {
        this.toogleEdit = false;
        this.getDetail()
    }


    beforeCreate() {
        this.toolsDataFlowService.postToolsDataFlows(this.dataFlow).subscribe(
            data => {
                this.dataForm.setValue(data.data);
                this.id_data_flow = data.data.id_data_flow;
                console.log(data.data.id_data_flow);
                this.getDetail()
            }, err => {
                this.Pesanservice.pesanWarningForm('Gagal di load data');
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }


    dataForm: FormGroupTypeSafe<Data_flow>;
    form() {
        this.dataForm = this.fb.group<Data_flow>(
            {
                name_flow: new FormControl(''),
                tabel_name: new FormControl(''),
                id_data_flow: new FormControl(''),
                default_status: new FormControl(''),
                for_module: new FormControl(''),
                close_status: new FormControl(''),
                id_kantor: new FormControl(''),
                id_divisi: new FormControl(''),
                id_departemen: new FormControl(''),
                data_flow_status: new FormControl(''),
                created_at: new FormControl(''),
                description: new FormControl(''),
                updated_at: new FormControl(''),
                validasi: new FormControl(''),
                id_user: new FormControl(''),
            }
        )
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.Pesanservice.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }


        this.dataFlow = this.dataForm.value;
        this.toolsDataFlowService.putToolsDataFlowsId({ id: this.id_data_flow, body: this.dataFlow }).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.Pesanservice.pesanWarningForm('Gagal di simpan');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )
    }


    goToBack() {

        this.router.navigate(['/users/approval/data-flow/list'])
    }


}
